import React,{Fragment, useState, useEffect} from 'react';
import {Link} from "react-router-dom";
import Spinner from './Spinner';
import axios from 'axios'

import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import Card from '@material-ui/core/Card';

const PokeCardFilter = ({classes, filter}) => {
	const [busqueda, setBusqueda] = useState([]); 
	const [image, setImage] = useState(`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/1.png`); 
 	
	React.useEffect(() =>{
		 obtenerDatos();
	},[filter.length])
		// `https://pokeapi.co/api/v2/pokemon/${filter}`
	const obtenerDatos = async () => {
		axios.get(`https://pokeapi.co/api/v2/pokemon/${filter}`).then(res =>{
			console.log(res.data);
			setBusqueda(res.data);
			setImage(`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${
              res.data.id
            }.png`);
		}).catch((error) => {
			console.log(error)
		})
 
	}
	return (
		<Fragment>
		{busqueda.id?(
			<div className={classes.paper}>
			 <Grid item key={busqueda.id} xs={12} sm={6} md={4}>
				<Card className={classes.card}>
				{setImage ? (
					<CardMedia 
						className={classes.cardMedia}
						image={image}
						title={busqueda.name}
						/> ) : null
						}
						<CardContent className={classes.cardContent}>
						<Typography gutterBottom variant="h5" component="h2" align="center" >
						
						<Button 
							component={ Link } 
							to={`/poke/${busqueda.id}`}  
							variant="contained" 
							className={classes.root}>
							{busqueda.name}
						</Button>
						</Typography>
						  
					</CardContent>
				</Card>
			</Grid>
			</div>
			): (
			<div className={classes.paper}>
				<Spinner />
			</div>
			)
		}
		</Fragment>
	);
}


export default PokeCardFilter;
