import React,{Fragment, useState, useEffect} from 'react';
import { useParams } from "react-router-dom";

 
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import axios from 'axios'

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  small: {
    width: theme.spacing(5),
    height: theme.spacing(5),
  },
  large: {
    width: theme.spacing(25),
    height: theme.spacing(25),
  },
  shiny: {
    width: theme.spacing(10),
    height: theme.spacing(10),
  },
   paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
}));

export default function PokeInfo(){
	const classes = useStyles();
	const {id} = useParams();
	const [pokemon, setPokemon] = useState([]); //puede llamarse con React.useState, sin enl import {...}
	const [pokemonShiny, setPokemonShiny] = useState([]); //puede llamarse con React.useState, sin enl import {...}


	let url = "https://github.com/PokeAPI/sprites/blob/master/sprites/pokemon/other-sprites/official-artwork/"
	let image = `${url}${id}.png?raw=true` 

	React.useEffect(() =>{
		 obtenerDatos();
	}, [])

	const obtenerDatos = async () => {
		axios.get(`https://pokeapi.co/api/v2/pokemon/${id}`).then(res =>{
			console.log(res.data);
			setPokemon(res.data);
			setPokemonShiny(res.data.sprites);
		}).catch((error) => {
            console.log(error)
        })
 
	}

	return (
		<Fragment>
		<Container  maxWidth="md">
			<div className={classes.paper}>
			<div className={classes.root}>
				<Avatar alt="Remy Sharp" src={image} className={classes.large} />
				<Avatar alt="Remy Sharp" src={pokemonShiny.front_shiny} className={classes.shiny}/>
			</div>
			{
				pokemon?(
					<Fragment>
						<h1>{pokemon.name}</h1>
					</Fragment>
		 		): (null)
			}
		 		 
			</div>
		</Container>
		</Fragment>
	)
}