import React,{Fragment, useState, useEffect} from 'react';
import Contenedor from './components/pages/Contenedor'
import PokeInfo from './components/pages/PokeInfo'
import PokeCardFilter from './components/pages/PokeCardFilter'

import PrimarySearchAppBar from './components/layout/partials/PrimarySearchAppBar'
import Footer from './components/layout/partials/Footer'


import CssBaseline from '@material-ui/core/CssBaseline';
import {  makeStyles } from '@material-ui/core/styles';


import {
	Switch,
	Route,
} from "react-router-dom";


const useStyles = makeStyles((theme) => ({
	icon: {
		marginRight: theme.spacing(2),
	},
	heroContent: {
		backgroundColor: theme.palette.background.paper,
		padding: theme.spacing(8, 0, 6),
	},
	heroButtons: {
		marginTop: theme.spacing(4),
	},
	cardGrid: {
		paddingTop: theme.spacing(8),
		paddingBottom: theme.spacing(8),
	},
	card: {
		height: '100%',
		display: 'flex',
		flexDirection: 'column',
	},
	cardMedia: {
		paddingTop: '86.25%', // 16:9
	},
	cardContent: {
		flexGrow: 1,
	},
	paper: {
    	marginTop: theme.spacing(8),
    	display: 'flex',
    	flexDirection: 'column',
    	alignItems: 'center',
  	},
	footer: {
		backgroundColor: theme.palette.background.paper,
		padding: theme.spacing(6),
	},
	 root: {
	    background: 'linear-gradient(45deg, #FF0239 30%, #FF5353 90%)',
	    borderRadius: 15,
	    border: 0,
	    color: 'white',
	    height: 48,
	    padding: '0 30px',
	    boxShadow: '0 3px 5px 2px rgba(247, 194, 205, 0.3)',
	  },
}));

function App() {
	const classes = useStyles();

	const [filter, setFilter] = useState(""); 
	
 	const handleSearchChange = (e) => {
	    setFilter(e.target.value.toLowerCase());
	   
	};
 
	// --------------------
	return (
		<Fragment>
			<CssBaseline />
			<PrimarySearchAppBar handleSearchChange={handleSearchChange}/>
			{
				filter.length>0  && <PokeCardFilter classes={classes} filter={filter}/>
			}
			<main>
				<Switch>
					<Route path="/poke/:id">
						<PokeInfo />
					</Route>

					<Route path="/reactPokemon" exact>
					{
					 	!filter.length>0 && <Contenedor classes={classes}/>
					}
					</Route>
				</Switch>
			</main>
			<Footer footer={classes.footer} />
		</Fragment>
	)
}

export default App;
