import React,{Fragment, useState, useEffect} from 'react';
import PokeCard from './PokeCard';
import axios from 'axios'
import Pagination from '../../components/Pagination'

 
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';

export default function Contenedor({classes}){
	const [pokemon, setPokemon] = useState([]);
	const [currentPageUrl, setCurrentPageUrl] = useState("https://pokeapi.co/api/v2/pokemon");
	const [nextPageUrl, setNextPageUrl] = useState();
	const [prevPageUrl, setPrevPageUrl] = useState();
	const [loading, setLoading] = useState(true);

	// --------------------
	useEffect(()=>{
		setLoading(true)
		let cancel
		axios.get(currentPageUrl,{
			cancelToken: new axios.CancelToken(c => cancel = c)			
		}).then(res =>{
			setLoading(false)
			console.log(res.data.results);
			setNextPageUrl(res.data.next)
			setPrevPageUrl(res.data.previous)
			setPokemon(res.data.results)
		}).catch((error) => {
            console.log(error)
        })

	 return () => cancel()
  }, [currentPageUrl])


	function gotoNextPage() {
		setCurrentPageUrl(nextPageUrl)
	  }

	 function gotoPrevPage() {
		setCurrentPageUrl(prevPageUrl)
	 }


	return (
		<Fragment>
			<Container className={classes.cardGrid} maxWidth="md">
				{/* End hero unit */}
				<Grid container spacing={4}>
					{pokemon.map((card, index) => {
						let url = "https://github.com/PokeAPI/sprites/blob/master/sprites/pokemon/other-sprites/official-artwork/"
						let pokeIndex = card.url.split('/')[card.url.split('/').length - 2]
						let image = `${url}${pokeIndex}.png?raw=true` 
						return <PokeCard 
							key={index} 
							pokeIndex={pokeIndex} 
							card={card} 
							image={image} 
							classes={classes} 
						 
						/>
					})}
				</Grid>
			</Container>
			<Pagination 
				gotoNextPage={nextPageUrl ? gotoNextPage : null}
				gotoPrevPage={prevPageUrl ? gotoPrevPage : null}
						/>
			
		</Fragment>
	)
}